          <div class="edit-option">
            <span>{{ $i18n.t('home.changeKyc') }}</span>
            <span :class="!isPreferenceActive || !hasPerm('IC_CHANGE_MY_KYC_BTN') ? 'cursor-disabled' : ''">
              <svg-icon
                icon-class="right"
