              ></svg-icon>
            </span>
          </div>

          <div class="edit-option">
            <span>{{ $i18n.t('home.auditLog') }}</span>
            <span :class="!isPreferenceActive ? 'cursor-disabled' : ''">
              <svg-icon
                icon-class="right"
                :class="['edit_icon', !isPreferenceActive ? 'disabled' : '']"
                @click="toAuditLog"
              ></svg-icon>
            </span>
          </div>
        </aside>
      </el-col>

      <el-col :span="18" class="right-col">
        <span v-if="!isPreferenceActive" class="crumbs-title">
          <svg-icon icon-class="left_small" class="crumbs-title-icon"></svg-icon>
          <i class="preference" @click="returnPreference"> {{ $t('home.myPreference') }} / </i>
          <i class="active">{{ $t($route.meta.title) }}</i>
        </span>
        <div class="content-right">
          <el-tabs v-if="isPreferenceActive" v-model="activeName">
            <el-tab-pane label="KYC Info" name="first">
              <kyc-info v-if="orgOperatorInfo.orgOperatorId" :identityId="orgOperatorInfo.orgOperatorId"></kyc-info>
            </el-tab-pane>

            <el-tab-pane label="Role" name="second">
              <span class="tab-title">
                {{ $t('home.roleTitle') }}
              </span>
              <table-page ref="roleTableData" size="small" :columns="roleColumns" :outSideTableData="roleTableData">
                <template slot-scope="scope" slot="ownEffectiveTime">
                  <span>{{ scope.data.row.effectiveTime | localTimeFormat('DD/MM/YYYY') }}</span>
                </template>

                <template slot-scope="scope" slot="ownExpiredTime">
                  <span>{{ scope.data.row.expiredTime | localTimeFormat('DD/MM/YYYY') }}</span>
                </template>
              </table-page>
            </el-tab-pane>

            <el-tab-pane label="Security" name="third">
              <span class="tab-title">{{ $t('home.credentialTitle') }}</span>
              <table-page
                ref="securityTableData"
                size="small"
                :columns="securityColumns"
                :outSideTableData="securityTableData"
              >
                <template slot-scope="scope" slot="ownCredentialTypeName">
                  <span>{{ parseCredentialTypeName(scope.data.row.credentialTypeName) }}</span>
                </template>
                <template slot-scope="scope" slot="ownCredentialStatus">
                  <span class="status-circle" :style="statusStyle(scope.data.row.credentialStatus)"></span>
                  <span>{{ parseCredentialStatus(scope.data.row.credentialStatus) }}</span>
                </template>
                <template slot-scope="scope" slot="ownExpiredTime">
                  <span>{{ scope.data.row.expireTime | localTimeFormat('DD/MM/YYYY') }}</span>
                </template>
              </table-page>
            </el-tab-pane>
          </el-tabs>
          <router-view></router-view>
        </div>
      </el-col>
    </el-row>
    <!-- changePassword弹窗 -->
    <portal-dialog
      width="448px"
      :visible="editPwdVisible"
      :customFooter="false"
      :cancelText="$t('home.reset')"
      :confirmText="$t('home.submit')"
      :cancelBtnDisable="pwdCancelBtnDisable"
      :confirmBtnDisable="pwdConfirmBtnDisable"
      @cancel-btn-click="editPwdCancelHandle"
      @confirm-btn-click="editPwdConfirmHandle"
      @closed-dialog="editPwdClose"
      :title="$t('home.changePwd')"
    >
      <el-form
        slot="content"
        :model="editPwdForm"
        ref="editPwdForm"
        :rules="editPwdRules"
        :hide-required-asterisk="true"
        class="edit-password_form"
      >
        <el-form-item prop="oldPassword" :label="$i18n.t('auth.oldPassword')">
          <el-input size="small" type="password" v-model="editPwdForm.oldPassword" placeholder="Please enter">
            <svg-icon v-if="oldPasswordCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
          </el-input>
        </el-form-item>

        <el-form-item prop="newPassword" :label="$i18n.t('auth.newPassword')">
          <el-input
            size="small"
            type="password"
            v-model="editPwdForm.newPassword"
            @focus="newPasswordFocus"
            placeholder="Please enter"
          >
            <svg-icon v-if="newPasswordCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
          </el-input>
          <div v-show="showTips" class="newPassword-tips">
            {{ $t('home.newPwdTips', { obj: minimumLength + '~' + maximumLength }) }}
          </div>
        </el-form-item>

        <el-form-item prop="confirmPassword" :label="$i18n.t('auth.confirmPassword')">
          <el-input size="small" type="password" v-model="editPwdForm.confirmPassword" placeholder="Please enter">
            <svg-icon v-if="confirmPasswordCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
          </el-input>
        </el-form-item>
      </el-form>
    </portal-dialog>

    <!-- changeQA弹窗 -->
    <portal-dialog
      width="708px"
      :visible="changeQAVisible"
      :customFooter="true"
      @closed-dialog="changeQAClosedHandle"
      :title="$t('home.changeQA')"
    >
      <div slot="content">
        <section v-show="step === 1" class="confirm-pwd">
          <div class="operator-title">
            <span>{{ $t('home.dialogUserName') }}</span>
            <span> {{ orgOperatorInfo.operatorName }}</span>
          </div>
          <el-form ref="confirmPwdForm" :model="confirmPwdForm">
            <el-form-item prop="password" :label="$i18n.t('auth.password')" :rules="passwordRules">
              <el-input
                id="password"
                size="small"
                type="password"
                v-model="confirmPwdForm.password"
                placeholder="Please enter"
              />
            </el-form-item>
          </el-form>
        </section>
        <section v-show="step === 2" class="change-pwd">
          <el-form :model="questionAnswerForm" ref="questionAnswerForm">
            <el-row :gutter="20" v-for="(item, index) in questionAnswerList" :key="index">
              <el-col :span="12">
                <el-form-item
                  :prop="'question' + (index + 1)"
                  :label="$i18n.t('auth.question', { obj: index + 1 })"
                  :rules="{ validator: duplicateQuestionVail, trigger: 'change' }"
                  required
                >
                  <el-select
                    v-model="questionAnswerForm['question' + (index + 1)]"
                    size="small"
                    filterable
                    allow-create
                    clearable
                    placeholder="Please select"
                  >
                    <el-option
                      v-for="item in questionOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    >
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item
                  :prop="'answer' + (index + 1)"
                  :label="$i18n.t('auth.answer', { obj: index + 1 })"
                  :rules="{ validator: answerVail, trigger: 'blur' }"
                >
                  <el-input
                    size="small"
                    type="password"
                    v-model="questionAnswerForm['answer' + (index + 1)]"
                    placeholder="Please enter"
                  >
                  </el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </section>
      </div>
      <div slot="footer" class="footer">
        <el-button
          v-show="step === 1"
          :disabled="!confirmPwdForm.password"
          class="next-btn"
          size="small"
          type="primary"
          @click="nextHandle"
        >
          {{ $t('auth.next') }}
        </el-button>
        <div v-show="step === 2" class="change-pwd-btn">
          <el-button type="text" class="return-btn" @click="changeQAReturnHandle">
            {{ $t('home.returnBtn') }}
          </el-button>
          <el-button plain :disabled="!checkQAFormExist" size="small" @click="resetBtn">
            {{ $t('home.reset') }}
          </el-button>
          <el-button :disabled="!checkQAFormExist" size="small" v-preventReClick type="primary" @click="submitBtn">
            {{ $t('home.submit') }}
          </el-button>
        </div>
      </div>
    </portal-dialog>

    <!-- changPIN弹窗 -->
    <portal-dialog
      width="448px"
      :visible="changePinVisible"
      :customFooter="true"
      @closed-dialog="changePinCloseAction"
      :title="$t('home.changePin')"
      :subtitle="$t('home.changePinSubtitle')"
    >
      <div slot="content" class="change-pin-content">
        <section v-show="changePinStep === 1">
          <div class="operator-title">
            <span>{{ $t('home.dialogUserName') }}</span>
            <span> {{ orgOperatorInfo.operatorName }}</span>
          </div>
          <el-form ref="pinConfirmPwdForm" :model="pinConfirmPwdForm">
            <el-form-item prop="password" :label="$i18n.t('auth.password')" :rules="passwordRules">
              <el-input
                id="password"
                size="small"
                type="password"
                v-model="pinConfirmPwdForm.password"
                placeholder="Please enter"
              />
            </el-form-item>
          </el-form>
        </section>
        <section v-show="changePinStep === 2">
          <el-form :model="changePinForm" ref="changePinForm" :rules="changePinRules" class="change-pin-form">
            <el-form-item prop="currentPin" :label="$i18n.t('home.currentPin')" required>
              <el-input type="password" size="small" v-model="changePinForm.currentPin" placeholder="Please enter">
                <svg-icon v-if="currentPinCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
              </el-input>
            </el-form-item>

            <el-form-item prop="newPin" :label="$i18n.t('home.newPin')" required>
              <el-input type="password" size="small" v-model="changePinForm.newPin" placeholder="Please enter">
                <svg-icon v-if="newPinCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
              </el-input>
            </el-form-item>

            <el-form-item prop="confirmPin" :label="$i18n.t('home.confirmPin')" required>
              <el-input type="password" size="small" v-model="changePinForm.confirmPin" placeholder="Please enter">
                <svg-icon v-if="confirmPinCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
              </el-input>
            </el-form-item>
          </el-form>
        </section>
      </div>

      <div slot="footer" class="change-pin-footer">
        <el-button
          v-show="changePinStep === 1"
          class="pin-next-btn"
          size="small"
          type="primary"
          :disabled="!pinConfirmPwdForm.password"
          @click="changePinNextHandle"
        >
          {{ $t('auth.next') }}
        </el-button>
        <div v-if="changePinStep === 2">
          <el-button type="text" class="return-btn" @click="changePinReturnHandle">
            {{ $t('home.returnBtn') }}
          </el-button>

          <el-button plain size="small" @click="changePinResetHandle">
            {{ $t('home.reset') }}
          </el-button>

          <el-button size="small" type="primary" v-preventReClick @click="changePinSubmitHandle">
            {{ $t('home.submit') }}
          </el-button>
        </div>
      </div>
    </portal-dialog>

    <!-- 是否返回myPreference页面弹窗 -->
    <portal-dialog
      width="448px"
      :visible="returnPreferenceVisible"
      @closed-dialog="returnPreferenceCancel"
      :title="$t('home.returnPreferenceTitle')"
      @cancel-btn-click="returnPreferenceCancel"
      @confirm-btn-click="returnPreferenceConfirm"
    >
      <span slot="title">{{ $t('home.returnPreferenceSubtitle') }}</span>
    </portal-dialog>
  </div>
</template>

<script>
import PortalDialog from '@/components/portal-dialog';
import SvgAvator from '@/components/svg-avatar/index.vue';
import {
  changePasswordOnline,
  authenticate,
  changeOperatorQA,
  getOrgOperatorDetail,
  changeOrgOperatorPin,
  checkCurrentPin,
  queryNewPinRule,
} from '@/views/home/api';
import { passwordRule, logout, queryQuestions, queryOperatorQuestions } from '@/views/auth/api';
import { loadLanguageList } from '@/views/identifier-center/api/api-organization.js';
import TablePage from '@/components/tablePage';
import KycInfo from '../../components/personal-kyc-info.vue';
import cookie from '@/common/utils/cookie';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
import { mapGetters } from 'vuex';

export default {
  name: 'PersonalSetting',
  components: {
    PortalDialog,
    TablePage,
    KycInfo,
    SvgAvator,
  },
  data() {
    let newPinVail = async (rule, value, callback) => {
      if (value) {
        const ruleList = await queryNewPinRule();
        const { minimumLength, maximumLength } = ruleList;
        let minLength = null;
        let maxLength = null;
        let credentialFormatType = '';
        if (ruleList && Object.keys(ruleList).length !== 0) {
          minLength = minimumLength;
          maxLength = maximumLength;
          credentialFormatType = ruleList.credentialFormatType;
        }
        if (credentialFormatType === 'Numeric_only') {
          let numRegex = /^[0-9]+$/;
          if (!numRegex.test(value)) {
            this.newPinCheck = false;
            return callback(new Error('Please enter a number.'));
          }
        }
        if (value.length < minLength || value.length > maxLength) {
          this.newPinCheck = false;
          return callback(new Error(`Please enter a number between ${minLength} and ${maxLength}.`));
        }
        this.newPinCheck = true;
        callback();
      } else {
        this.newPinCheck = false;
        callback();
      }
    };

    let currentPinVail = (rule, value, callback) => {
      if (value) {
        let data = {
          orgOperatorId: this.orgOperatorInfo.orgOperatorId,
          credentialValue: value,
        };
        checkCurrentPin(data).then((res) => {
          if (res.header && res.header.success) {
            this.currentPinCheck = true;
            callback();
          } else {
            let errorTips = res.header.responseDesc || 'currentPin error';
            this.currentPinCheck = false;
            callback(new Error(errorTips));
          }
        });
      } else {
        this.currentPinCheck = false;
        callback();
      }
    };

    let confirmPinVail = (rule, value, callback) => {
      if (value) {
        if (this.changePinForm.newPin !== value) {
          this.confirmPinCheck = false;
          callback(new Error('The two PIN are inconsistent.'));
        } else {
          this.confirmPinCheck = true;
          callback();
        }
      } else {
        this.confirmPinCheck = false;
        callback();
      }
    };

    let newPassworldVail = async (rule, value, callback) => {
      if (value) {
        if (value.length < this.minimumLength || value.length > this.maximumLength) {
          this.newPasswordCheck = false;
          this.showTips = false;
          return callback(new Error(`Please enter a number between ${this.minimumLength} and ${this.maximumLength}.`));
        } else {
          this.showTips = false;
          this.newPasswordCheck = true;
          callback();
        }
      } else {
        this.newPasswordCheck = false;
        this.showTips = false;
        callback(new Error('Please enter content'));
      }
    };
    let confirmPasswordVail = (rule, value, callback) => {
      if (value) {
        if (this.editPwdForm.newPassword !== value) {
          this.confirmPasswordCheck = false;
          return callback(new Error('The two passwords are inconsistent.'));
        } else {
          this.confirmPasswordCheck = true;
          callback();
        }
        callback();
      }
    };
    let dialogPasswordVail = (rule, value, callback) => {
      if (value) {
        authenticate({ password: value }).then((res) => {
          if (res.header && res.header.success) {
            callback();
          } else {
            callback(new Error('Please Check The Password And Try Agin'));
          }
        });
      } else {
        callback();
      }
    };
    let oldPasswordVail = (rule, value, callback) => {
      if (value) {
        authenticate({ password: value }).then((res) => {
          if (res.header && res.header.success) {
            this.oldPasswordCheck = true;
            callback();
          } else {
            this.oldPasswordCheck = false;
            callback(new Error('Please Check The Password And Try Agin'));
          }
        });
      } else {
        this.oldPasswordCheck = false;
        callback(new Error('Please enter content'));
      }
    };
    return {
      roleColumns: [
        {
          label: this.$t('home.role'),
          prop: 'name',
          width: '200px',
          align: 'left',
        },
        {
          label: this.$t('home.effectiveDate'),
          prop: 'effectiveTime',
          width: '200px',
          align: 'left',
          __slotName: 'ownEffectiveTime',
        },
        {
          label: this.$t('home.expirationDate'),
          prop: 'expiredTime',
          width: '200px',
          align: 'left',
          __slotName: 'ownExpiredTime',
        },
        {
          label: this.$t('home.description'),
          prop: 'description',
          align: 'left',
        },
      ],
      securityTableData: [],
      securityColumns: [
        {
          label: this.$t('home.name'),
          prop: 'credentialTypeName',
          width: '200px',
          align: 'left',
          __slotName: 'ownCredentialTypeName',
        },
        {
          label: this.$t('home.status'),
          prop: 'credentialStatus',
          width: '200px',
          align: 'left',
          __slotName: 'ownCredentialStatus',
        },
        {
          label: this.$t('home.expirationDate'),
          prop: 'expireTime',
          align: 'left',
          __slotName: 'ownExpiredTime',
        },
      ],
      activeName: 'first',
      editPwdVisible: false,
      editPwdForm: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: '',
      },
      showTips: false,
      editPwdRules: {
        oldPassword: [{ validator: oldPasswordVail, trigger: 'blur' }],
        newPassword: [{ validator: newPassworldVail, trigger: 'blur' }],
        confirmPassword: [{ validator: confirmPasswordVail, trigger: 'blur' }],
      },
      oldPasswordCheck: false,
      newPasswordCheck: false,
      confirmPasswordCheck: false,
      pwdCancelBtnDisable: false,
      changeQAVisible: false,
      step: 1,
      confirmPwdForm: {
        password: '',
      },
      confirmPwdRules: {
        password: [],
      },
      questionAnswerForm: {},
      questionOptions: [],
      isFirstTips: true,
      operatorQuestions: [],
      questionAnswerList: [],
      answerMinLength: 0,
      sameAnswerFlag: 0,
      orgOperatorInfo: {},
      roleTableData: [],
      accessChannel: '',
      isShowChangePin: false,
      changePinVisible: false,
      changePinStep: 1,
      pinConfirmPwdForm: {
        password: '',
      },
      passwordRules: [{ validator: dialogPasswordVail, trigger: 'submit' }],
      changePinRules: {
        currentPin: [{ validator: currentPinVail, trigger: 'blur' }],
        newPin: [{ validator: newPinVail, trigger: 'blur' }],
        confirmPin: [{ validator: confirmPinVail, trigger: 'blur' }],
      },
      changePinForm: {
        currentPin: '',
        newPin: '',
        confirmPin: '',
      },
      currentPinCheck: false,
      newPinCheck: false,
      confirmPinCheck: false,
      returnPreferenceVisible: false,
      minimumLength: 8,
      maximumLength: 20,
    };
  },
  computed: {
    ...mapGetters(['currentUsername']),

    statusStyle() {
      return (status) => {
        switch (status) {
          case '0':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '1':
            return { 'background-color': `${this.GlobalStyleVariables.statusNoticeColor}` };
          case '3':
            return { 'background-color': `${this.GlobalStyleVariables.statusErrorColor}` };
          case '2':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          default:
            return;
        }
      };
    },

    pwdConfirmBtnDisable() {
      let isDisabled = true;
      if (this.newPasswordCheck && this.confirmPasswordCheck) {
        isDisabled = false;
      }
      return isDisabled;
    },

    checkQAFormExist() {
      let exist = false;
      for (let key in this.questionAnswerForm) {
        if (this.questionAnswerForm[key]) {
          exist = true;
        }
      }
      return exist;
    },

    isPreferenceActive() {
      return this.$route.path === '/personalSettings';
    },
  },
  filters: {
    getFirstStr(value) {
      return value.charAt(0);
    },
  },
  created() {
    this.getOperatorInfo();
  },
  methods: {
    returnPreferenceCancel() {
      this.returnPreferenceVisible = false;
    },

    returnPreferenceConfirm() {
      this.returnPreferenceVisible = false;
      this.$router.push({
        path: '/personalSettings',
      });
    },

    changeQAReturnHandle() {
      this.step = 1;
    },

    changePinReturnHandle() {
      this.changePinStep = 1;
    },

    changePinSubmitHandle() {
      this.$refs['changePinForm'].validate((valid) => {
        if (valid) {
          let data = {
            receiverIdentityId: cookie.get('initiatorIdentityId'),
            oldPIN: this.changePinForm.currentPin,
            newPIN: this.changePinForm.confirmPin,
          };
          changeOrgOperatorPin(data).then((res) => {
            if (res.header && res.header.success) {
              this.$message.success('Operation succeeded.');
              this.changePinStep = 1;
              this.changePinVisible = false;
              this.currentPinCheck = false;
              this.newPinCheck = false;
              this.confirmPinCheck = false;
              this.$refs['pinConfirmPwdForm'].resetFields();
              this.$refs['changePinForm'].resetFields();
            }
          });
        }
      });
    },

    changePinResetHandle() {
      this.$refs['changePinForm'].resetFields();
    },

    changePinNextHandle() {
      this.$refs['pinConfirmPwdForm'].validate((valid) => {
        if (valid) {
          this.changePinStep = 2;
        }
      });
    },

    changePinCloseAction() {
      this.changePinVisible = false;
      this.changePinStep = 1;
      this.$refs['pinConfirmPwdForm'].resetFields();
      this.$refs['changePinForm'].resetFields();
    },

    returnPreference() {
      let baseStore = this.$store.state.baseStore;
      if (baseStore.isKycFormChange['personalSettings']) {
        this.returnPreferenceVisible = true;
      } else {
        this.$router.push({
          path: '/personalSettings',
        });
      }
    },

    changePin() {
      if (!this.isPreferenceActive) {
        return;
      }
      this.changePinVisible = true;
    },

    toChangeKyc() {
      if (!this.isPreferenceActive) {
        return;
      }
      let data = encodeURIComponent(
        JSON.stringify({
          identityId: this.orgOperatorInfo.orgOperatorId,
        })
      );
      this.$router.push({
        path: '/personalSettings/personalChangeKyc',
        query: {
          data,
        },
      });
    },

    toAuditLog() {
      if (!this.isPreferenceActive) {
        return;
      }
      this.$store.commit('baseStore/SET_KYCFORM_CHANGE', { personalSettings: false });
      let title = {
        title: this.$t('IDC.auditlog.AuditLogTitle'),
        desc: this.$t('home.auditLogTitle'),
      };
      let data = encodeURIComponent(
        JSON.stringify({
          initiatorId: this.orgOperatorInfo.orgOperatorId,
          type: 'personalSettings',
          title,
          hasSplitline: true,
        })
      );
      this.$router.push({
        path: '/personalSettings/personalAuditLog',
        query: {
          data,
        },
      });
    },

    getLanguageAndParse(languageList, langCode) {
      let languageName = '';
      for (let i = 0; i < languageList.length; i++) {
        let item = languageList[i];
        if (langCode && langCode === item.languageCode) {
          languageName = item.languageName;
        }
      }
      return languageName;
    },

    parseAccessChannelToStr(accessChannelArr) {
      let accessChannelStr = '';
      let parseKey = [
        { value: '1005', label: 'Web' },
        { value: '1007', label: 'Handset' },
        { value: '1010', label: 'API' },
      ];
      if (accessChannelArr.length > 0) {
        for (let i = 0; i < parseKey.length; i++) {
          if (accessChannelArr.includes(parseKey[i].value)) {
            accessChannelStr += ',' + parseKey[i].label;
          }
        }
      }
      return accessChannelStr.replace(/,/, '');
    },

    // 答案规则校验
    answerVail(rule, value, callback) {
      if (value) {
        if (value.length < this.answerMinLength) {
          callback(new Error(`Must be more than ${this.answerMinLength} characters`));
        } else {
          if (!this.sameAnswerFlag) {
            let answerArr = [];
            for (let key in this.questionAnswerForm) {
              if (key.indexOf('answer') !== -1) {
                answerArr.push(this.questionAnswerForm[key]);
              }
            }
            answerArr.sort();
            for (let i = 0; i < answerArr.length; i++) {
              if (answerArr[i] && answerArr[i] === answerArr[i + 1]) {
                callback(new Error('cannot configure the same answer'));
              }
            }
          }
        }
        callback();
      }
    },

    // 重复Question校验
    duplicateQuestionVail(rule, value, callback) {
      if (value) {
        let questionArr = [];
        for (let key in this.questionAnswerForm) {
          if (key.indexOf('question') !== -1) {
            questionArr.push(this.questionAnswerForm[key]);
          }
        }
        questionArr.sort();
        for (let i = 0; i < questionArr.length; i++) {
          if (questionArr[i] && questionArr[i] === questionArr[i + 1]) {
            callback(new Error('cannot configure the same question'));
          }
        }
      }
      callback();
    },

    changeQAClosedHandle() {
      this.$refs['confirmPwdForm'].resetFields();
      this.changeQAVisible = false;
      this.step = 1;
    },

    // 回显系统QA数量与初始化QA表单
    renderQuestionAnswer(number) {
      this.questionAnswerList = [];
      for (let i = 0; i < number; i++) {
        let obj = {
          ['question' + (i + 1)]: '',
          ['answer' + (i + 1)]: '',
        };
        this.questionAnswerList.push(obj);
        this.$set(this.questionAnswerForm, 'question' + (i + 1), '');
        this.$set(this.questionAnswerForm, 'answer' + (i + 1), '');
      }
      this.getOperatorQuestions();
    },

    getOperatorInfo() {
      getOrgOperatorDetail().then(async (res) => {
        if (res.header && res.header.success) {
          let orgOperatorInfo = res.orgOperatorInfo;
          for (let key in orgOperatorInfo) {
            this.$set(this.orgOperatorInfo, key, orgOperatorInfo[key]);
          }
          let languageRes = await loadLanguageList();
          if (languageRes.header && languageRes.header.success) {
            this.orgOperatorInfo.language = this.getLanguageAndParse(languageRes.list, this.orgOperatorInfo.language);
          }

          for (let key in res.roles) {
            this.$set(this.roleTableData, key, res.roles[key]);
          }
          for (let key in res.credentials) {
            this.$set(this.securityTableData, key, res.credentials[key]);
          }
          this.accessChannel = this.parseAccessChannelToStr(orgOperatorInfo.accessChannel);
        }
        if (this.accessChannel.indexOf('Handset') !== -1) {
          this.isShowChangePin = true;
        }
      });
    },

    parseCredentialTypeName(val) {
      if (!val) {
        return '-';
      }
      switch (val) {
        case '01':
          return 'Password';
        case '02':
          return 'PIN';
        case '03':
          return 'Secret word';
        case '04':
          return 'Digital certification';
        case '05':
          return 'Third party password';
        case '06':
          return 'API Password';
        default:
          return '-';
      }
    },

    parseCredentialStatus(val) {
      if (!val) {
        return '-';
      }
      switch (val) {
        case '0':
          return 'Pending Change';
        case '1':
          return 'NORMAL';
        case '2':
          return 'Suspended';
        case '3':
          return 'Locked';
        default:
          return '-';
      }
    },

    showIdentityStatus(val) {
      return STATUS_MAP[val] || '-';
    },

    editPwdClose() {
      this.editPwdVisible = false;
      this.isFirstTips = true;
      this.confirmPasswordCheck = false;
      this.newPasswordCheck = false;
      this.$refs['editPwdForm'].resetFields();
    },
    newPasswordFocus() {
      if (this.isFirstTips) {
        passwordRule().then((res) => {
          if (res) {
            this.minimumLength = res.minimumLength;
            this.maximumLength = res.maximumLength;
          }
        });
        this.showTips = true;
        this.isFirstTips = false;
      }
    },
    editPwd() {
      if (!this.isPreferenceActive) {
        return;
      }
      this.editPwdVisible = true;
    },
    editQA() {
      if (!this.isPreferenceActive) {
        return;
      }
      this.changeQAVisible = true;
    },
    editPwdCancelHandle() {
      this.isFirstTips = true;
      this.oldPasswordCheck = false;
      this.newPasswordCheck = false;
      this.confirmPasswordCheck = false;
      this.$refs['editPwdForm'].resetFields();
    },

    editPwdConfirmHandle() {
      this.$refs['editPwdForm'].validate((valid) => {
        if (valid) {
          let data = {
            oldPassword: this.editPwdForm.oldPassword,
            newPassword: this.editPwdForm.newPassword,
          };
          changePasswordOnline(data).then((res) => {
            if (res.header && res.header.success) {
              this.$message.success('Password changed successfully.');
              this.editPwdVisible = false;
              this.$refs['editPwdForm'].resetFields();
              logout().then((res) => {
                if (res.header && res.header.success) {
                  this.$router.push({
                    path: '/login',
                  });
                }
              });
            }
          });
        }
      });
    },

    nextHandle() {
      this.$refs['confirmPwdForm'].validate((valid) => {
        if (valid) {
          this.getQuestions();
        }
      });
    },

    // 获取系统QA
    getQuestions() {
      this.questionOptions = [];
      queryQuestions().then((res) => {
        if (res.header && res.header.success) {
          let orgOperatorQuestionAnswer = res.orgOperatorQuestionAnswer;
          this.answerMinLength = res.questionPolicy.answerMinLength;
          this.sameAnswerFlag = res.questionPolicy.sameAnswerFlag || 0;
          let questionNumber = res.questionPolicy.questionNumber || 0;
          if (questionNumber > 0) {
            this.questionOptions = orgOperatorQuestionAnswer.map((item) => {
              return {
                value: item.question,
                label: item.question,
              };
            });
            this.renderQuestionAnswer(questionNumber);
            this.step = 2;
          } else {
            this.$message.error(
              'The question and answer cannot be configured because the number of questions and answers is 0.'
            );
            this.changeQAVisible = false;
            this.step = 1;
          }
        }
      });
    },

    getOperatorQuestions() {
      let data = {
        initiatorIdentityId: this.orgOperatorInfo.orgOperatorId,
        receiverIdentityId: this.orgOperatorInfo.orgOperatorId,
      };
      queryOperatorQuestions(data).then((res) => {
        if (res.header && res.header.success) {
          let orgOperatorQuestionAnswer = res.orgOperatorQuestionAnswer;
          if (orgOperatorQuestionAnswer.length > 0) {
            for (let i = 0; i < orgOperatorQuestionAnswer.length; i++) {
              this.questionAnswerForm['question' + (i + 1)] = orgOperatorQuestionAnswer[i].question;
            }
          }
        }
      });
    },

    resetBtn() {
      this.$refs['questionAnswerForm'].resetFields();
    },

    submitBtn() {
      this.$refs['questionAnswerForm'].validate((valid) => {
        if (valid) {
          let questionAnswer = this.questionAnswerList.map((item, index) => {
            return {
              answer: this.questionAnswerForm['answer' + (index + 1)],
              question: this.questionAnswerForm['question' + (index + 1)],
            };
          });
          let data = {
            orgOperatorQuestionAnswer: questionAnswer,
          };
          changeOperatorQA(data).then((res) => {
            if (res.header && res.header.success) {
              this.$message.success('Operation succeeded.');
              this.changeQAVisible = false;
              this.$refs['questionAnswerForm'].resetFields();
              this.step = 1;
            }
          });
        }
      });
    },
  },
};
</script>

